/* https://codepen.io/noahblon/pen/OyajvN */
input[type="range"] { 
  -webkit-appearance: none;
  position: relative;
  overflow: hidden;
  height: 16px;
  width: 75px;
  cursor: pointer;
  border-radius: 2px; /* iOS */
}

::-webkit-slider-runnable-track {
  background: #ddd;
}

/*
* 1. Set to 0 width and remove border for a slider without a thumb
*/
::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 15px; /* 1 */
  height: 40px;
  background: #fff;
  box-shadow: -100vw 0 0 100vw var(--blue);
  border: 2px solid #999; /* 1 */
}

::-moz-range-track {
  height: 40px;
  background: #ddd;
}

::-moz-range-thumb {
  background: #fff;
  height: 40px;
  width: 15px;
  border: 3px solid #999;
  border-radius: 0 !important;
  box-shadow: -100vw 0 0 100vw var(--blue);
  box-sizing: border-box;
}

::-ms-fill-lower { 
  background: var(--blue);
}

::-ms-thumb { 
  background: #fff;
  border: 2px solid #999;
  height: 40px;
  width: 20px;
  box-sizing: border-box;
}

::-ms-ticks-after { 
  display: none; 
}

::-ms-ticks-before { 
  display: none; 
}

::-ms-track { 
  background: #ddd;
  color: transparent;
  height: 40px;
  border: none;
}

::-ms-tooltip { 
  display: none;
}
